/**
 *   Name: Bizagi Desktop Render MultiSelect Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the multiSelect render class to adjust to desktop devices
 */


// Extends from render Desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.multiSelect", bizagi.rendering.render.base.prototype);
// Extends from render Desktop base
bizagi.rendering.desktop.multiSelect.extend("bizagi.rendering.desktop.multiSelect", bizagi.rendering.multiSelect.prototype);
// Extends itself
bizagi.rendering.desktop.multiSelect.extend("bizagi.rendering.desktop.multiSelect", {
    MORE_OPTION_WIDTH: 40
}, {

    /**
     * Update or init the element data
     * @param data
     */
    initializeData: function (data) {
        // Call base
        this._super(data);

        this.isNewRenderForm = true;
    },

    /**
     * Template method to implement in each device to customize each render after processed
     */
    postRender: function () {
        var $control = this.getControl();
        this.multiSelect = $(".bz-multiselect", $control);

        // Call base
        this._super();
    },

    /**
     * Returns the html templated element
     * @returns {*}
     */
    renderElement: function () {
        return this._super("render-base");
    },

    /**
     * Template method to implement in each device to customize each render after processed in read-only mode
     */
    postRenderReadOnly: function () {
        var $control = this.getControl();
        this.multiSelect = $(".bz-multiselect", $control);

        // Call base
        this._super();
    },

    /**
     * Sets the value in the rendered control
     * @param value
     */
    setDisplayValue: function (value) {
        var self = this;

        // Set internal value
        self._super(value);

        setTimeout(function () {
            self.addSelectedElements();
        }, 100);
    },

    /**
     * Template method to implement in each device to customize the render's behaviour to add handlers
     */
    configureHandlers: function () {
        var self = this;
        var $control = self.getControl();

        $(".bz-multiselect", $control)
            .on("click, mousedown.multiselect", function(evt){
                if($(evt.target).hasClass("bz-multiselect__tags") || $(evt.target).hasClass("bz-multiselect__arrow")){
                    self.onToggleDataView(evt)
                }
            });
        
        $('ul.bz-multiselect__tags', $control)
            .on("click", "li div.bz-btn-icn", this.onUnselectElement.bind(this))
            .on("click", "li.bz-multiselect__plus", this.onShowSelectedElements.bind(this));
    },

    /**
     * Template method to implement in each device to customize the render's behaviour to add handlers when the control is readonly
     */
    configureHandlersReadOnly: function () {
        var $control = this.getControl();

        $('ul.bz-multiselect__tags', $control)
            .on("click", "li.bz-multiselect__plus", this.onShowSelectedElements.bind(this));
    },

    /**
     * addSelectedElements
     */
    addSelectedElements: function () {
        var self = this;
        var $control = this.getControl();
        var values = this.getDataValue() || [];
        var totalValues = values.length;
        var $tagsContainer = $('ul.bz-multiselect__tags', $control);
        var editable = this.properties.editable;
        var template = this.renderFactory.getTemplate("list-multiselect-tag");

        $tagsContainer.empty();

        if (totalValues > 0) {

            if (this.getFormContainer().params.printversion) {
                $control.html(values.map(function (el) {
                    return self.formatItem(el.value);
                }).join(", "));
            } else {

                for (var i = 0; i < totalValues; i++) {
                    var item = values[i];
                    var data = {id: item.id, value: item.value, editable: editable};
                    var $ChildToAdd = $.tmpl(template, data, {
                        formatItem: self.formatItem
                    });

                    $tagsContainer.append($ChildToAdd);

                    // Process the control in the layout
                    this.processControlLayout(this.properties.valueFormat || {});

                    if (!this.canAddMoreItems($tagsContainer)) {
                        // Remove last item added
                        $tagsContainer
                            .children()
                            .last()
                            .remove();

                        // Add the view mode item
                        var $viewMore = $.tmpl(this.renderFactory.getTemplate("list-multiselect-view-more"), {
                            value: totalValues - i,
                            index: i
                        });

                        $tagsContainer.append($viewMore);
                        break;
                    }
                }
            }
        }

    },

    /**
     * Get data and render the float list view
     */
    renderFloatView: function () {
        var self = this;
        var template = self.renderFactory.getTemplate("list-multiselect-item");

        self.updateArrow();

        $.when(self.getData()).done(function (data) {
            try {
                var params = {datasource: data};
                var options = {
                    selected: self.getDataValue(),
                    isChecked: function (id) {
                        return self.markAsChecked.call(this, id);
                    },
                    formatItem: self.formatItem
                };

                var $floatList = self.showFloatListView(template, params, options);
                self.floatListHandlers($floatList);

            } catch (e) {
                bizagi.log(e.message);
            }
        });
    },

    /**
     * Show float list view
     * @param template
     * @param data
     * @param options
     */
    showFloatListView: function (template, data, options) {
        var self = this;
        var $control = self.getControl();
        var $containerControl = $control.closest(".ui-bizagi-control-wrapper");
        var $containerRender = $containerControl.closest(".bz-form-old-item");

        var $floatList = $.tmpl(template, data, options);
        var widthControl = self.multiSelect.width();

        $containerRender.addClass("bz-multiselect__visible");
        $containerControl.addClass("bz-multiselect__visible bz-multiselect__floats");
        $containerControl.append($floatList);

        $floatList.width(widthControl);

        $floatList.position({
            my: "left top",
            at: "left bottom",
            of: self.multiSelect,
            collision: "fit"
        }).hide();

        // Display view
        $floatList.show();

        // Stop bubbling outside the dropdown
        $floatList.on("mousedown", function (ev) {
            ev.preventDefault();
            return false;
        });

        // Add global handler to close the float view
        setTimeout(function(){
            $(document).one("mousedown.multiselect", function (evt) {
                $floatList.trigger('closeView');
            });
        }, 0);

        return $floatList;
    },

    /**
     * Destroy the float list view
     * @param $list
     */
    destroyFloatView: function ($list) {
        var $control = this.getControl();
        var $containerControl = $control.closest(".ui-bizagi-control-wrapper");
        var $containerRender = $containerControl.closest(".bz-form-old-item");

        if ($containerControl.hasClass("bz-multiselect__visible"))
            $containerControl.removeClass("bz-multiselect__visible");

        if ($containerControl.hasClass("bz-multiselect__floats"))
            $containerControl.removeClass("bz-multiselect__floats");

        if ($containerRender.hasClass("bz-multiselect__visible"))
            $containerRender.removeClass("bz-multiselect__visible");

        $list.remove();
    },

    /**
     * Update the float list view
     */
    closeFloatView: function () {
        var $control = this.getControl();
        var $containerControl = $control.closest(".ui-bizagi-control-wrapper");
        var $containerRender = $containerControl.closest(".bz-form-old-item");

        var $listView = $($containerRender).find(".bz-multiselect__content");

        $listView.hide();

        this.updateArrow();
        this.destroyFloatView($listView)
    },

    updateArrow: function () {
        var $control = this.getControl();

        $control.find(".bz-multiselect__arrow")
            .toggleClass("bz-multiselect__arrow-collapse");
    },

    floatViewIsCollapsed: function () {
        var $listView = $(".bz-multiselect__content", this.element);

        return $listView && $listView.length > 0;
    },

    changeRequired: function (argument) {
        this.properties.required = argument;
        this.changeRequiredLabel(argument);
    },

    changeRequiredLabel: function (argument) {
        var control = this.getControl();

        if (control.length > 0) {
            if (this.getMode() !== "execution") {
                if (bizagi.util.parseBoolean(argument) === true && this.properties.editable) {
                    control.addClass("control-required");
                } else {
                    control.removeClass("control-required");
                }
            } else {
                if (bizagi.util.parseBoolean(argument) === true && !this.hasValue() && this.properties.editable) {
                    control.addClass("control-required");
                } else {
                    control.removeClass("control-required");
                }
            }
        }

        this.triggerRenderValidate();
    },

    changeCellRequired: function (argument) {
        this.changeRequired(argument);
    },

    onUnselectElement: function (ev) {
        var $element = $(ev.target);
        var idToRemove = $element.data("id");

        // Remove element
        this.removeElement({id: idToRemove});
        this.addSelectedElements();
    },

    onShowSelectedElements: function (ev) {
        var self = this;
        var $element = $(ev.currentTarget);
        var index = $element.data('index');

        var valuesToShow = (this.getDataValue() || []).slice(index);
        var template = this.renderFactory.getTemplate("list-multiselect-tags");
        var $listView = this.showFloatListView(template, {
            datasource: valuesToShow,
            editable: self.properties.editable
        }, {
            formatItem: self.formatItem
        });

        var $tagElements = $('ul.bz-multiselect__tags', $listView);

        $tagElements.on("click, mousedown", "li div.bz-btn-icn", function (ev) {
            self.onUnselectElement(ev);
            $listView.remove();
            ev.stopPropagation();
        });

        $listView.on("closeView", function () {
            $tagElements.off("click", "li div.bz-btn-icn", self.onUnselectElement.bind(self))
            $listView.remove();
        });

    },

    onToggleDataView: function (evt) {
        this.floatViewIsCollapsed() ? this.closeFloatView() : this.renderFloatView();
    },

    floatListHandlers: function ($floatListContext) {
        var self = this;
        var $listContainer = $floatListContext.find(".bz-multiselect__list li");
        var checkbox = $listContainer.find("input[type=checkbox]");

        $floatListContext.find(".bz-multiselect__search_input").bind("change keypress keyup mousedown", function (evt) {
            evt.stopPropagation();

            self.filterList(this.value, $listContainer);
        });

        checkbox.change(function () {
            var $context = $(this);
            var isChecked = $context.is(":checked");
            var id = $context.data("id");
            var element = self.findItemById(id);
            var value = element.value;

            var newValue = {id: id, value: value};

            if (isChecked) {
                self.addElement(newValue);
            } else {
                self.removeElement(newValue);
            }

            self.addSelectedElements();
        });

        $floatListContext.on("closeView", function () {
            self.closeFloatView();

            $(document).unbind("mousedown.multiselect");
        });
    },

    /**
     * Template method to get the label element
     * @returns {jQuery|HTMLElement}
     */
    getLabel: function () {
        return $("label", this._super());
    }

});

