// Extends from render Desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.getUser", bizagi.rendering.getUser.prototype);
// Extends itself
bizagi.rendering.desktop.getUser.extend("bizagi.rendering.desktop.getUser", {}, {
    
    /**
     * Template method to implement in each device to customize each render after processed
     */
    postRender: function () {
        var self = this;
        var opt = {};
        self.input = $(".ui-bizagi-render-text", self.getControl());

        if (self.properties.value != null && self.properties.value.length > 0) {
            self.setDisplayValue(self.properties.value[0]);
        }
        // Call base
        this._super();
    },

    /**
     * Template method to implement in each device to customize the render's behaviour to add handlers
     */
    configureHandlers: function () {
        var self = this;
        var control = self.getControl();
        self.bindSearchUserPlugin(control);
        $('#btn-clear-user', control).on('click', function () {
            self.clearValue();
        });
    },

    /**
     * Set display value
     */
    setDisplayValue: function (data, triggerEvents) {
        var self = this;
        triggerEvents = triggerEvents == false ? triggerEvents : true;
        var value = (typeof (data) !== "undefined") ? data.value : null;

        if (self.properties.editable) {
            self.input.val(value);
        }

        self.setValue(data, triggerEvents);
    },

    /**
     * Clear data control
     */
    clearValue: function () {
        var self = this;

        if (self.input) {
            self.setDisplayValue("",false);
        }
    },

    /**
     * Binds the search user plugin
     */
    bindSearchUserPlugin: function (control) {
        var self = this;
        var options = {
            title: self.getResource("render-plugin-search-users-title"),
            maximize: false,
            filters: ["domain", "userName", "fullName"],
            displayNames: [
                self.getResource("render-plugin-search-users-domain"),
                self.getResource("render-plugin-search-users-user-name"),
                self.getResource("render-plugin-search-users-full-name")],
            searchButtonName: self.getResource("render-plugin-search-users-search-button-name"),
            clearButtonName: self.getResource("render-plugin-search-users-clear-button-name"),
            valuesToSelect: ["iduser", "user"], //"iduser","username","user","domain","email"
            selectItem: function () {
                var deferred = $.Deferred();
                deferred.done(function (data) {
                    self.onItemSelectedSuccess(data);
                }).fail(function (error) {
                    bizagi.log(error);
                });
                return deferred;
            },
            searchUsers: function (filterData) {
                if (self.properties.allowDelegatedUser) {
                    return self.dataService.getUsersListFromPreferences(filterData);
                } else {
                    return self.dataService.getUsersListForStakeholders(filterData);
                }
            },
            getTableTemplate: function (usersResults, currentPage, pagination, pagesArray) {
                var content = self.renderFactory.getTemplate("bizagi.plugin.search.user");
                var usersTableTmpl = $.tmpl(content, {
                    users: usersResults,
                    links: [self.getResource("render-plugin-search-users-select-link")],
                    page: currentPage,
                    pagination: pagination,
                    pagesArray: pagesArray
                });
                return usersTableTmpl;
            },
            showError: function (error) {
                bizagi.log(error);
            },
            showMessageBox: function (message) {
                bizagi.showMessageBox(bizagi.localization.getResource(message), "Bizagi", "warning");
            }
        };

        if (self.properties.allowDelegatedUser) {
            options.editingUser = self.properties.idUser;
        }

        opt = options;

        // Allows add new user when creating the pluggin
        var params = self.getParams();
        if (typeof params.isEntityForm !== "undefined" && params.isEntityForm) {
            options.allowAddUser = bizagi.menuSecurity.UserAdmin || false;
            options.dataService = self.dataService;
            options.renderFactory = self.renderFactory;
        }

        $('#btn-get-user', control).bizagiSearchUser(options);
    },

    /**
     * returns the opt var for unit testing
     */
    getOptions: function () {
        return opt;
    },
    /**
     *
     * @param itemSelected
     */
    onItemSelectedSuccess: function (itemSelected) {
        var self = this;
        var data = {
            id: itemSelected.iduser, 
            value: itemSelected.user 
        };

        self.setDisplayValue(data);
    },
    
    changeRequired: function (argument) {
        this.properties.required = argument;
        this.changeRequiredLabel(argument);
    },

    changeRequiredLabel: function (argument) {
        var control = this.getControl();

        if (control.length > 0) {
            if (this.getMode() !== "execution") {
                if (bizagi.util.parseBoolean(argument) == true && this.properties.editable) {
                    control.addClass('control-required');
                } else {
                    control.removeClass('control-required');
                }
            }
            else {
                if (bizagi.util.parseBoolean(argument) == true && !this.hasValue() && this.properties.editable) {
                    control.addClass('control-required');
                } else {
                    control.removeClass('control-required');
                }
            }
        }

        this.triggerRenderValidate();
    },

    changeRequiredControl: function (argument) {
        this.changeRequiredLabel(argument);
    }
});
