/**
 *   Name: Bizagi Desktop Render Button Extension
 *   Author: Bizagi UI Forms
 *   Comments:
 *   -   This script will redefine the button render class to adjust to desktop devices
 */

// Extends from render Desktop base 
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.button", bizagi.rendering.render.base.prototype);
// Extends from render Desktop base
bizagi.rendering.desktop.button.extend("bizagi.rendering.desktop.button", bizagi.rendering.button.prototype);
// Extends itself
bizagi.rendering.desktop.button.extend("bizagi.rendering.desktop.button", {}, {

    /**
     * Template method to implement in each device to customize each render after processed
     * @returns {}
     */
    postRender: function () {
        var self = this;
        var control = self.getControl();
        var button = $(":button", control);
        var buttonInput = $(control.find("input"));

        // Stylize button
        button.button();

        buttonInput.addClass("ui-button--ellipsis");
        buttonInput.attr("title", $(control.find("input")).val());
    },

    /**
     * Template method to implement in each device to customize the render's behaviour to add handlers
     * @returns {}
     */
    configureHandlers: function () {
        var self = this;
        var button = $(":button", self.getControl());

        // Call base
        self._super();

        // Bind event
        button.on("click", function () {
            //disable button to avoid multiple clicks
            $("input", self.element).attr("disabled", true);

            // Process button actions
            self.processButton();

            setTimeout(function () {
                //allow button to be clickeable again
                $("input", self.element).removeAttr("disabled");
            }, 1500);
        });
    },

    /**
     * Method to render non editable values
     * @returns {}
     */
    postRenderReadOnly: function () {
        // Execute the same as post-render
        this.postRender();

        // Set as disabled
        var button = $(":button", this.getControl());
        button.button("option", "disabled", "true");
    },

    /**
     * Process the button actions workflow
     * @returns {}
     */
    processButton: function () {
        var self = this;
        var properties = self.properties;
        var mode = self.getMode();

        if (properties.editable) {

            if (typeof (self.getFormContainer) != "undefined")

                var form = self.getFormContainer();

            if (bizagi.override.validateGridsOnButtonClick != false && mode == "execution" && form &&
                !form.hasValidGrids([])) {
                form.validationController.performValidations();
            } else {
                // Check if action need conformation
                if (properties.needsUserConfirmation) {
                    var htmlMessage = $("<div>").append(properties.userConfirmationMessage);
                    var defer = $.Deferred();

                    $(htmlMessage).dialog({
                        modal: true,
                        width: 500,
                        height: 250,
                        draggable: false,
                        resizable: true,
                        maximize: false,
                        title: properties.caption || properties.displayName,
                        buttons: [{
                            text: bizagi.localization.getResource("confirmation-box-ok"),
                            click: function () {
                                defer.resolve();
                                self.triggerRenderChange({pressed: true});
                                $(this).dialog("close");
                            }
                        }, {
                            text: bizagi.localization.getResource("confirmation-box-cancel"),
                            click: function () {
                                defer.reject();
                                $(this).dialog("close");
                            }
                        }]
                    });

                    if (!properties.preventDefault) {
                        $.when(defer.promise()).done(function () {
                            self.runButtonAction();
                        });
                    }
                } else {
                    if (!properties.preventDefault)
                        self.runButtonAction();

                    // Trigger change
                    self.triggerRenderChange({pressed: true});
                }
            }
        }
    }
});

