/**
 *   Name: Bizagi Render Geolocation control
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for form link renders
 */
// Extends from render desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.geolocation", bizagi.rendering.geolocation.prototype);
// Extends itself
bizagi.rendering.desktop.geolocation.extend("bizagi.rendering.desktop.geolocation", {}, {

    /**
     * Method to customize each render after processed in read-only mode
     */
    renderReadOnly: function () {
        return this.renderControl();
    },

    postRenderReadOnly: function () {
        this.postRender();
    },

    postRender: function () {
        // Call base
        this._super();

        this.cordinate = $(".ui-geolocation-cordinate", this.getControl());
    },

    /**
     * Template method to implement in each device to customize the render's behaviour to add handlers
     */
    configureHandlers: function () {
        var self = this;

        if (self.isExecutionMode()) {
            var control = self.getControl();

            // If the control doesn't have a value set the position
            if (self.value == null) {
                self.setDisplayValue().then(function () {
                }).fail(function (msg) {
                    self.cordinate.html("<span style='color: red'>" + msg + "</span>");
                });
            }

            $(".ui-bizagi-render-geolocation", control).bind("click", function () {
                self.cordinate.empty();
                // This retun a promise if is usefull
                self.setDisplayValue().then(function () {
                }).fail(function (msg) {
                    self.cordinate.html("<span style='color: red'>" + msg + "</span>");
                });
            });
        }
    },

    /**
     * Sets the value in the rendered control
     * @param value
     * @returns {*}
     */

    clearDisplayValue: function () {
        var self = this;
        var control = self.getControl();
        var input = control.find("span");

        if (input.length > 0) {
            input.val("");
        }
    },

    setDisplayValue: function (value) {
        var self = this;
        var def = new $.Deferred();

        if (value != null && value !== "") {
            self.cordinate.html(self.performPrecision(value));

            // Set internal value
            self.setValue(value, false);
            def.resolve();
        } else {

            var onSuccess = function (position) {
                var coordinate = position.coords.latitude + " " + position.coords.longitude;

                self.cordinate.html(self.performPrecision(coordinate));

                // Set internal value
                self.setValue(coordinate, false);
                def.resolve();
            };

            // onError Callback receives a PositionError object
            var onError = function (msg) {
                self.cordinate.html(self.properties.value);
                def.reject(msg);
            };

            self.getCurrentPosition(onSuccess, onError);
        }

        return def.promise();
    }

});