    /**
     * Returns the appropiate column based on the render type
     */
    getColumn: function (params) {
        var self = this;
        var type = params.type;
        var data = params.data;
        var properties = data.properties;
        var columnParams = $.extend(params, {
            renderFactory: this,
            dataService: params.dataService || this.dataService,
            singleInstance: bizagi.util.isEmpty(params.singleInstance) ? true : params.singleInstance
        });

        if (type === "columnText") {
            var isExtended = bizagi.util.parseBoolean(data.properties.isExtended) || false;

            if (isExtended) {
                // Creates one extended render
                columnParams.decorated = bizagi.rendering.desktop.extendedText;
            } else {
                // Creates one normal text render
                columnParams.decorated = bizagi.rendering.desktop.text;
            }
            return new bizagi.rendering.desktop.columns.text(columnParams);
        }

        if (type === "columnSearchList") {
            columnParams.decorated = bizagi.rendering.desktop.searchList;
            return new bizagi.rendering.desktop.columns.searchList(columnParams);
        }

        if (type === "columnNumberScientificNotation") {
            columnParams.decorated = bizagi.rendering.desktop.numberScientificNotation;
            return new bizagi.rendering.desktop.columns.numberScientificNotation(columnParams);
        }

        if (type === "columnNumber") {
            if (properties && properties.dataType === 29) {
                columnParams.decorated = bizagi.rendering.desktop.numberScientificNotation;
                return new bizagi.rendering.desktop.columns.numberScientificNotation(columnParams);
            } else {
                columnParams.decorated = bizagi.rendering.desktop.number;
                return new bizagi.rendering.desktop.columns.number(columnParams);
            }
        }

        if (type === "columnMoney") {
            columnParams.decorated = bizagi.rendering.desktop.money;
            return new bizagi.rendering.desktop.columns.number(columnParams);
        }

        if (type === "columnDate") {
            columnParams.decorated = bizagi.rendering.desktop.date;
            return new bizagi.rendering.desktop.columns.date(columnParams);
        }

        if (type === "columnBoolean") {
            var display = data.properties.display || "option";

            if (display === "check") {
                // Creates one check render
                columnParams.decorated = bizagi.rendering.desktop.check;
                if (params.data.properties.isExclusive) {
                    return new bizagi.rendering.desktop.columns.exclusiveCheck(columnParams);
                }
            } else {
                // Creates one yes-no render
                columnParams.decorated = bizagi.rendering.desktop.yesno;
            }

            return new bizagi.rendering.desktop.columns.column(columnParams);
        }

        if (type === "columnCombo") {
            columnParams.decorated = bizagi.rendering.desktop.combo;
            return new bizagi.rendering.desktop.columns.combo(columnParams);
        }

        if (type === "columnRadio") {
            columnParams.decorated = bizagi.rendering.desktop.radio;
            return new bizagi.rendering.desktop.columns.radio(columnParams);
        }

        if (type === "columnList") {
            columnParams.decorated = bizagi.rendering.desktop.list;
            return new bizagi.rendering.desktop.columns.list(columnParams);
        }

        if (type === "columnHidden") {
            columnParams.decorated = bizagi.rendering.desktop.hidden;
            return new bizagi.rendering.desktop.columns.column(columnParams);
        }

        if (type === "columnUpload") {
            columnParams.decorated = bizagi.rendering.desktop.upload;
            return new bizagi.rendering.desktop.columns.upload(columnParams);
        }

        if (type === "columnImage") {
            columnParams.decorated = bizagi.rendering.desktop.image;
            return new bizagi.rendering.desktop.columns.image(columnParams);
        }

        if (type === "columnUploadEcm") {
            columnParams.decorated = bizagi.rendering.desktop.ecm;
            return new bizagi.rendering.desktop.columns.uploadecm(columnParams);
        }

        if (type === "columnLink") {
            columnParams.decorated = bizagi.rendering.desktop.link;
            return new bizagi.rendering.desktop.columns.link(columnParams);
        }

        if (type === "columnFormLink") {
            columnParams.decorated = bizagi.rendering.desktop.formLink;
            return new bizagi.rendering.desktop.columns.link(columnParams);
        }

        if (type === "columnUserfield") {
            columnParams.decorated = bizagi.rendering.desktop.userfieldWrapper;
            return new bizagi.rendering.desktop.columns.column(columnParams);
        }

        if (type === "columnSearch") {
            columnParams.decorated = bizagi.rendering.desktop.search;
            return new bizagi.rendering.desktop.columns.search(columnParams);
        }

        if (type === "columnLabel") {
            columnParams.decorated = bizagi.rendering.desktop.label;
            return new bizagi.rendering.desktop.columns.column(columnParams);
        }

        if (type === "columnReadonly") {
            columnParams.decorated = bizagi.rendering.desktop.label;
            return new bizagi.rendering.desktop.columns.readonly(columnParams);
        }

        if (type === "columnDocument") {
            columnParams.decorated = bizagi.rendering.desktop.document;
            return new bizagi.rendering.desktop.columns.document(columnParams);
        }

        if (type === "columnLetter") {
            columnParams.decorated = bizagi.rendering.desktop.letter;
            return new bizagi.rendering.desktop.columns.letter(columnParams);
        }

        if (type === "columnButton") {
            columnParams.decorated = bizagi.rendering.desktop.button;
            return new bizagi.rendering.desktop.columns.button(columnParams);
        }

        if (type === "columnActions") {
            columnParams.decorated = bizagi.rendering.desktop.gridActions;
            return new bizagi.rendering.desktop.columns.actions(columnParams);
        }

        // No type supported
        bizagi.log(type + " not supported in render factory", data, "error");
        return null;
    },

