    /*
    *   Returns the appropiate search render based on the type
    */
    getSearchRender: function (params) {
        var type = params.type;
        var data = params.data;
        var searchParams = $.extend(params, {
            renderFactory: this,
            dataService: params.dataService || this.dataService
        });

        if (type == "searchText") {
            var isExtended = bizagi.util.parseBoolean(data.properties.isExtended) || false;

            if (isExtended) {
                // Creates one extended render
                return new bizagi.rendering.desktop.extendedText(searchParams);
            }

            // Creates one normal text render
            return new bizagi.rendering.desktop.text(searchParams);
        }

        if (type == "searchBoolean") {
            var display = data.properties.display || "option";

            if (display == "check") {
                // Creates one check render
                return new bizagi.rendering.desktop.check(searchParams);
            }

            // Creates one yes-no render
            return new bizagi.rendering.desktop.yesno(searchParams);
        }

        if (type == "searchNumber") {
            if (data.properties.dataType == 29) {
                // Creates a Scientific Notation render
                return new bizagi.rendering.desktop.numberScientificNotation(searchParams);
            }
            return new bizagi.rendering.desktop.number(searchParams);
        }

        if (type == "searchMoney") {
            return new bizagi.rendering.desktop.money(searchParams);
        }

        if (type == "searchDate") {
            return new bizagi.rendering.desktop.date(searchParams);
        }

        if (type == "searchCombo") {
            return new bizagi.rendering.desktop.combo(searchParams);
        }

        if (type == "searchSuggest") {
            return new bizagi.rendering.desktop.search(searchParams);
        }

        if (type == "searchLabel") {
            return new bizagi.rendering.desktop.label(searchParams);
        }

        // No type supported
        bizagi.log(type + " not supported in render factory", data, "error");
        return null;
    },

