// Extends itself
bizagi.rendering.render.extend("bizagi.rendering.desktop.gridActions", {}, {

    renderControl: function () {
        var self = this;
        var template = self.renderFactory.getTemplate("gridActions");
        var hasActions = self.grid && self.grid.hasActions() || false;

        // Render template
        var html = $.fasttmpl(template, {hasActions: hasActions});
        return html;
    }
    
});
