/*
*   Name: Bizagi Desktop Render Label Extension
*   Author: UI Forms Team
*   Comments:
*   -   This script will redefine the label render class to adjust to desktop devices
*/

// Extends from render Desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.label", bizagi.rendering.label.prototype);
// Extends itself
bizagi.rendering.desktop.label.extend("bizagi.rendering.desktop.label", {}, {

    /**
     * Method to render non editable values
     */
    renderReadOnly: function() {
        this.setValue(this.properties.displayName);
        return this._super();
    },

    /**
     * Template method to implement in each device to customize each render after processed     
     */
    postRender: function() {
        // Apply a css class
        this.getLabel().addClass("ui-bizagi-render-label");
    },

    /**
     * Method to render non editable values     
     */
    postRenderReadOnly: function() {
        // Apply a css class
        this.getLabel().addClass("ui-bizagi-render-label");
    }

});
