/**
 * Desktop definition of range money control
 *
 * @author: Andres Fernando Munoz
 */

// Extends from range desktop base
bizagi.rendering.desktop.range.extend("bizagi.rendering.desktop.rangeMoney", bizagi.rendering.rangeMoney.prototype);
// Extends itself
bizagi.rendering.desktop.rangeMoney.extend("bizagi.rendering.desktop.rangeMoney", {}, {

	/**
	 * Render a specific implementation for Desktop device
	 */
	postRender: function() {
        var self = this;
        self.defineRangeControl();
	},

	/**
	 * Render actual value of control and set events
	 * @param data
	 */
	setDisplayValue: function(data) {
		var self = this;
		var control = self.getControl();
	}
});