/**
 *   Name: Bizagi Desktop Render Text Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the text render class to adjust to desktop devices
 */

// Extends from render desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.text", bizagi.rendering.text.prototype);
// Extends itself
bizagi.rendering.desktop.text.extend("bizagi.rendering.desktop.text", {}, {

    /**
     *   Template method to implement in each device to customize each render after processed
     */
    postRender: function () {
        // Call base
        this._super();

        // Define max length of element
        if (this.properties.maxLength > 0)
            this.input.attr("maxlength", this.properties.maxLength);
    },

    /**
     *   Sets the value in the rendered control
     * @param {} value
     * @returns {}
     */
    setDisplayValue: function (value) {
        var self = this;
        var decodedValue = bizagi.util.decodeURI(value);
        value = this.validateValue((typeof (decodedValue) == "string" ? decodedValue : ""), self.properties.maxLength);

        // Call base
        self.setValue(value);
        this._super(value);

        // Set value in input
        if (!bizagi.util.isEmpty(value) && this.properties.editable) {
            this.input && this.input.val(value);
        } else {
            if (value === null && this.properties.editable) {
                this.input && this.input.val("");
            }
        }
    },

    attachRetypeDouble: function () {
        var self = this;
        var element = self.element;

        var input = $("input", element);
        if (!input || input.length == 0) return;

        // Apply blur handler to check value
        input.blur(function () {
            var control = $(this);
            self.retypeDouble(control);
        });
    },

    changeRequired: function (argument) {
        this.properties.required = argument;
        this.changeRequiredLabel(argument);
    },

    changeRequiredLabel: function (argument) {
        var control = this.getControl();
        control.tooltip({disabled: true});

        if (control.length > 0) {
            if (this.getMode() !== "execution") {
                if (bizagi.util.parseBoolean(argument) == true && this.properties.editable) {
                    control.addClass("control-required");
                } else {
                    control.removeClass("control-required");
                }
            } else {
                if (bizagi.util.parseBoolean(argument) == true && !this.hasValue() && this.properties.editable) {
                    control.addClass("control-required");
                } else {
                    control.removeClass("control-required");
                }
            }
        }

        this.triggerRenderValidate();
    },

    changeCellRequired: function (argument) {
        this.changeRequired(argument);
    },

    retypeDouble: function (control) {
        var self = this;
        var currentValue = control.val();
        var isVisible = $(control).attr("type") !== "hidden" && $(control).css("display") !== "none" && $(control).css("visibility") !== "hidden";

        if (isVisible && $(this).data("newValue") == undefined) {

            if (!control.data("oldValue") || $(this).data("oldValue") === "") {
                // Check that there is something in the value or if a value has already been set
                if (currentValue === "" || currentValue === control.data("newValue")) return;

                self.createTooltip(control, currentValue, self.getResource("render-number-retype"))
            } else {
                // Check that there is something in the value
                if (currentValue === "") return;

                if (currentValue != control.data("oldValue")) {
                    self.setValue("");

                    self.createTooltip(control, "", self.getResource("render-text-retype-error"))
                } else {
                    control.data("newValue", currentValue);
                    control.data("oldValue", "");

                    // Destroy tooltips
                    control.tooltip("destroy");
                }
            }
        }
    },

    collectData: function (renderValues) {
        var self = this;

        // Add the render value
        var xpath = self.properties.xpath;

        // replace strange character
        var value = self.getValue();

        if (value != null && typeof (value) !== "undefined" && typeof (value) !== "number") {
            value = value.replaceAll(String.fromCharCode("030"), '');
            value = value.replaceAll(String.fromCharCode("018"), '');
            value = value.replaceAll(String.fromCharCode("016"), '');
        }

        if (self.controlValueIsChanged()) {
            // Filter by valid xpaths and valid values
            if (!bizagi.util.isEmpty(xpath)) {
                if (!bizagi.util.isEmpty(value)) {
                    renderValues[xpath] = value;
                } else if (value === "") {
                    renderValues[xpath] = "";
                }
            }
        }

        //Validate if the input has a retype configured and check that both values matches, oterwise delete the original value
        if (!self.validateInputRetype()) {
            renderValues[self.properties.xpath] = "";
        }
    }

});
