/*  
*   Name: Bizagi Desktop Render list Extension
*   Author: Bizagi UI Forms
*   Comments:
*   -   This script will redefine the list render class to adjust to desktop devices
*/

bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.list", bizagi.rendering.render.base.prototype);
// Extends from render desktop base
bizagi.rendering.desktop.list.extend("bizagi.rendering.desktop.list", bizagi.rendering.combo.prototype);
// Extends itself
bizagi.rendering.desktop.list.extend("bizagi.rendering.desktop.list", bizagi.rendering.list.prototype);
// Extends itself
bizagi.rendering.desktop.list.extend("bizagi.rendering.desktop.list", {}, {

    /**
     * Template method to implement in each device to customize each render after processed
     * @returns {} 
     */
    postRender: function() {
        var self = this;
        var control = self.getControl();
        var properties = self.properties;

        // Call base 
        self._super();

        // Set control container to behave as a block
        control.addClass("ui-bizagi-render-display-block");
        control.addClass("ui-bizagi-render-boolean-yesno");

        // Apply plugin
        self.list = $(".ui-bizagi-render-list", control);

        self.list.selectable2({
            closest: false,
            multiple: false,
            select: function(e, ui) {

                if (e.isPropagationStopped()) return;

                var selected = $("li[tabindex='0']", this);
                var value = $(selected).data("value");
                var label = $(selected).text();

                // Updates internal value
                var newValue = { id: value, value: label };
                self.setValue(newValue);
                self.selectedValue = newValue.value;
                self.focusSelectedItem(selected);

                e.stopPropagation();
            }
        });

        // Apply list size
        if (properties.listSize > 0) {
            var listSize = Number(properties.listSize) * 23;
            self.list.height(listSize + "px");
        }
    },

    focusSelectedItem: function(elList) {
        var self = this;

        $.when(self.ready()).done(function() {
            elList.focus(0);
        });
    },

    /**
     * Template method to implement in each device to customize the render's behaviour
     * to add handlers     
     * @returns {} 
     */
    configureHandlers: function() {        
        // Call base
        this._super();        
    },

    /**
     * Template method to implement in each device to customize the render's behaviour
     * when rendering in design mode
     * @returns {} 
     */
    configureDesignView: function() {
        var self = this;        

        // Call base
        self._super();

        // Un-bind selection handlers
        if (self.properties.editable) {
            self.list.unbind("mousedown.selectable");
            self.list.unbind("focus.selectable");
            self.list.unbind("blur.selectable");
            self.list.unbind("keydown.selectable");
        }
    },

    /**
     * Sets the value in the rendered control
     * @param {} value 
     * @returns {} 
     */
    setDisplayValue: function(value) {
        var self = this;
        value = this.mapValue(value);

        if (self.properties.editable) {
            if (value !== undefined && value.id !== "") {
                self.list.selectable2("select", "[id='ui-bizagi-list-" + value.id + "']");
            }
        }
    },

    /**
     * Sets the value in the rendered control
     * @returns {} 
     */
    clearDisplayValue: function() {
        var self = this;
        var control = self.getControl();
        var selected = control.find(".ui-state-active");

        if (selected.length > 0) {
            // Updates internal value
            var newValue = self.getValue();

            self.selectedValue = newValue.value;
            selected.removeClass("ui-state-focus ui-selected ui-state-active");
        } else if (!self.properties.editable) {
            $(control).html("<label class='readonly-control'></label>");
        }
    },

    /**
     * Returns the selected value in the template
     * @returns {} 
     */
    getSelectedValue: function() {        
        return this.selectedValue;
    },

    /**
     * Cleans current data
     * @returns {} 
     */
    cleanData: function() {
        var self = this;
        self.value = self.properties.value = self.selectedValue = { id: "", value: "" };
        self.clearDisplayValue();
    },

    getLabel: function(){
        return $("label", this._super());
    },

    getElementsToStylize: function($context){
        return $("ul li", $context);
    },

    changeRequired: function (argument) {
        this.properties.required = argument;
        this.changeRequiredLabel(argument);
    },

    changeRequiredLabel: function (argument) {
        var control = this.getControl();
        if (this.getMode() !== "execution") {
            if (bizagi.util.parseBoolean(argument) == true && this.properties.editable) {
                control.addClass("control-required");
            } else {
                control.removeClass("control-required");
            }
        } else {
            if (bizagi.util.parseBoolean(argument) == true && !this.hasValue() && this.properties.editable) {
                control.addClass("control-required");
            } else {
                control.removeClass("control-required");
            }
        }
        this.triggerRenderValidate();
    },
    
    changeCellRequired: function (argument) {
        this.changeRequired(argument);
    },

});
