/*
 *   Name: Bizagi Desktop Render Text Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the text render class to adjust to desktop devices
 */

// Extends from render desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.extendedText", bizagi.rendering.extendedText.prototype);
// Extends itself
bizagi.rendering.desktop.extendedText.extend("bizagi.rendering.desktop.extendedText", {

    //#region Side effects functions

    /**
     * Is triggered after the control exists in the DOM
     */
    postRender: function () {
        this._super();

        if (bizagi.util.isIE8()) {
            this.textarea.attr("cols", "5000");
        }
    },

    /**
     * Is triggered after the control exists in the DOM when it's readonly
     */
    postRenderReadOnly: function () {
        $(this.getControl()).addClass("ui-bizagi-text-extended-readonly");
    },

    /**
     *   Template method to implement in each device to customize the render's behaviour to add handlers
     */
    configureHandlers: function () {
        var self = this;
        var properties = self.properties;

        // Call base
        self._super();

        // Define max length of element
        if (properties.maxLength > 0) {

            self.textarea.attr("maxlength", properties.maxLength);
            self.actualMaxLength = properties.maxLength;

        }

        self.ready().done(function () {
            // auto extend
            if (properties.autoExtend) {
                var value = "";
                var minHeight = self.textarea.css("height");
                var valMinHeight;


                if (properties.maxLines === undefined) {
                    if (typeof minHeight == "string" && minHeight.search("px") !== -1) {
                        valMinHeight = Number(minHeight.split("px")[0]);

                        if (valMinHeight <= 150)
                            minHeight = "150px";
                    } else {
                        minHeight = "150px";
                    }
                }

                // set textarea styles
                self.textarea.css({overflow: "hidden", resize: "none", minHeight: minHeight});

                setTimeout(function () {
                    self.changeHeightAutoExtended(self.textarea[0]);
                }, 100);

                self.textarea.bind("keyup", function () {
                    self.changeHeightAutoExtended(this);
                });
            }
        });
    },

    /**
     *   Sets the value in the rendered control
     * @param {} value
     */
    setDisplayValue: function (value) {
        var self = this;
        var decodedValue = bizagi.util.decodeURI(value);
        value = this.validateValue((typeof (decodedValue) == "string" ? decodedValue : ""), self.properties.maxLength);

        // Call base
        self.setValue(value);
        this._super(value);

        // Set value in input
        var resolvedValue = $.br2nl(value);
        if (self.textarea) self.textarea.val(resolvedValue);
    },

    /**
     * Gets control's value
     * @param {} renderValues
     */
    collectData: function (renderValues) {
        var self = this;
        var properties = self.properties;

        // Add the render value
        var xpath = properties.xpath;

        // replace strange character
        var value = self.getValue();

        if (self.getValue() != null && typeof (self.getValue()) != "undefined") {
            value = self.getValue().replaceAll(String.fromCharCode("030"), '');
            value = self.getValue().replaceAll(String.fromCharCode("018"), '');
            value = self.getValue().replaceAll(String.fromCharCode("016"), '');
        }

        if ($.controlValueIsChanged(self)) {
            // Filter by valid xpaths and valid values
            if (!bizagi.util.isEmpty(xpath)) {
                if (!bizagi.util.isEmpty(value)) {
                    renderValues[xpath] = value;
                } else if (value === "") {
                    renderValues[xpath] = "";
                }
            }
        }

        // Validate if the input has a retype configured and check that both values matches,
        // otherwise delete the original value
        if (!self.validateInputRetype()) {
            renderValues[properties.xpath] = "";
        }
    },

    /**
     * Cleans current value
     */
    cleanData: function () {
        this.setValue("");
        this.setDisplayValue(this.getValue());        
    },

    /**
     * function virtual implements on children (desktop,smartphone...)
     */
    attachRetypeDouble: function () {
        var self = this;
        var element = self.element;

        var input = $("input, textarea", element);
        if (!input || input.length == 0) return;

        // Apply blur handler to check value
        input.blur(function () {
            var control = $(this);
            var previousValue = self.previousValue;
            var value = self.value;

            if (previousValue != null && value != previousValue) {
                if ($(this).data("newValue") != undefined && control.val() != $(this).data("newValue"))
                    $(this).data().newValue = undefined;
            }

            self.retypeDouble(control);
        });
    },

    /**
     * Changes required status
     * @param {} argument
     */
    changeRequired: function (argument) {
        this.properties.required = argument;
        this.changeRequiredLabel(argument);
    },

    /**
     * Changes label's required status
     * @param {} argument
     */
    changeRequiredLabel: function (argument) {
        var control = this.getControl();

        if (control.length > 0) {
            if (this.getMode() !== "execution") {
                if (bizagi.util.parseBoolean(argument) == true && this.properties.editable) {
                    control.addClass('control-required');
                } else {
                    control.removeClass('control-required');
                }
            } else {
                if (bizagi.util.parseBoolean(argument) == true && !this.hasValue() && this.properties.editable) {
                    control.addClass('control-required');
                } else {
                    control.removeClass('control-required');
                }
            }
        }

        this.triggerRenderValidate();
    },

    /**
     * Changes cell's required status
     * @param {} argument
     */
    changeCellRequired: function (argument) {
        this.changeRequired(argument);
    },

    /**
     * Validates the double value from retype
     * @param {jQuery Object} control jQuery Object
     */
    retypeDouble: function (control) {
        var self = this;
        var currentValue = control.val();
        var isVisible = $(control).attr("type") != "hidden" && $(control).css("display") != "none" && $(control).css("visibility") != "hidden";

        if (isVisible && $(this).data("newValue") == undefined) {

            if (!control.data("oldValue") || $(this).data("oldValue") == "") {
                // Check that there is something in the value or if a value has already been set
                if (currentValue == "" || currentValue == control.data("newValue")) return;

                self.createTooltip(control, currentValue, self.getResource("render-number-retype"))
            } else {
                // Check that there is something in the value
                if (currentValue == "") return;

                if (currentValue != control.data("oldValue")) {
                    self.setValue("");
                    self.createTooltip(control, "", self.getResource("render-text-retype-fail"))
                } else {
                    control.data("newValue", currentValue);
                    control.data("oldValue", "");
                    // Destroy tooltips
                    control.tooltip("destroy");
                }
            }
        }
    },

    /**
     * Changes control's height when it's auto extended
     * @param {Object} control HTML Object
     */
    changeHeightAutoExtended: function (control) {
        control.style.removeProperty("height");
        control.style.height = control.scrollHeight < (this.properties.maxLines * 20) ? (this.properties.maxLines * 20) : control.scrollHeight + "px";
    }

    //#endregion Side effects functions
});
