/**
 *   Name: Bizagi Desktop Render Link Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the link render class to adjust to desktop devices
 */

bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.link", bizagi.rendering.render.base.prototype);
// Extends from render desktop base
bizagi.rendering.desktop.link.extend("bizagi.rendering.desktop.link", bizagi.rendering.link.prototype);
// Extends itself
bizagi.rendering.desktop.link.extend("bizagi.rendering.desktop.link", {}, {

    /**
     * Template method to implement in each device to customize each render after processed
     * @returns {}
     */
    postRender: function () {
    },

    /**
     * Template method to implement in each device to customize the render's behaviour to add handlers
     * @returns {}
     */
    configureHandlers: function () {
        this.attachEvents()
    },

    /**
     * Template method to implement in each device to customize the render's behaviour
     * to add handlers when the control is readonly
     */
    configureHandlersReadOnly: function () {
        this.attachEvents();
    },

    /**
     * Solve action to link
     * @param args
     */
    linkSolver: function (args) {
        args = args || {};
        var self = this;
        var url = args.url || "";
        var target = args.target || "_blank";

        if (bizagi.override.linkPrefix === true && url) {
            if (url.indexOf("http") === -1 && url.indexOf("\\\\") === -1) {
                url = "//" + bizagi.util.trim(url);
            }
        }

        switch (args.linkType) {
            case "newwindow":
                var windowParamsPatter = "width=%s,height=%s,resizable,scrollbars=yes,status=1,toolbar=yes";
                var windowParams = printf(windowParamsPatter, window.screen.width, window.screen.height);
                self.windowObjReference = window.open(url, target, windowParams);
                break;
            case "newtab":
                self.windowObjReference = window.open(url, target);
                break;
            default:
                self.windowObjReference = window.open(url, target);
                break;
        }
    },

    /**
     * Method to render non editable values
     * @returns {}
     */
    postRenderReadOnly: function () {
        // Execute the same as post-render
        this.postRender();
    },

    /**
     * Configure event handlers
     */
    attachEvents: function (){
        var self = this;
        var control = self.getControl();
        var properties = self.properties;
        var link = $(".ui-bizagi-render-link", control);

        if (properties.value) {
            // Modify href
            link.attr("href", properties.value);
            link.attr("target", "_blank");

            link.click(function () {
                //if the container form of this component is detail, executes the link
                self.linkSolver({linkType: properties.linktarget, url: properties.value});
                return false;
            });
        } else {
            link.addClass("ui-state-disabled");
        }

        
        self.preventDefaultForAnchorTag();
    },

    processLayout: function (isLabelFormat) {
        if(!isLabelFormat){
             this._super(isLabelFormat, this.properties.textFormat);
        } else {
            if(typeof this.properties.textFormat.background !== "undefined"){
                this._super(isLabelFormat, { background: this.properties.textFormat.background });
            }
        }
    },

    getElementsToStylize: function($context){
        return $(".ui-bizagi-render-link", $context); 
    }
});
