/**
 *   Name: Bizagi Desktop Render Form Link Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the link render class to adjust to desktop devices
 */

// Extends from render Desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.formLink", {
    BA_ACTION_PARAMETER_PREFIX: "p_",
    BA_CONTEXT_PARAMETER_PREFIX: "h_",
    BA_PAGE_CACHE: "pageCacheId"
}, bizagi.rendering.formLink.prototype);
// Extends itself
bizagi.rendering.desktop.formLink.extend("bizagi.rendering.desktop.formLink", {}, {

    /**
     * Template method to implement in each device to customize the render's behaviour to add handlers
     */
    configureHandlers: function () {
        this.attachEvents();
    },

    /**
     * Template method to implement in each device to customize the render's behaviour
     * to add handlers when the control is readonly
     */
    configureHandlersReadOnly: function () {
        this.attachEvents();
    },

    openLinkRule: function (property, elLink) {
        var self = this;
        var def = new $.Deferred();

        // Save form data
        self.getPropertyValue(property, {})
            .done(function (dataRuleResp) {
                if (dataRuleResp.messages != null) {
                    var message = dataRuleResp.messages[0];
                    var form = self.getFormContainer();
                    if (dataRuleResp.type == "alertMessages"){ 
                        bizagi.modalError.show(message, 'workportal-general-word-information', false);
                    }else{
                        form.validationController.showErrorMessage(message);    
                    }
                    def.reject();
                } else {
                    if (property === "entryrule")
                        self.openLink(elLink);
                }
                def.resolve();
            }).fail(function (error) {
            def.reject();
        });

        return def.promise();
    },

    getPropertyValue: function (propertyName, params) {
        var self = this;
        var properties = self.properties;
        var defer = new $.Deferred();

        self.dataService.multiaction().getPropertyData({
            url: properties.dataUrl,
            xpath: properties.xpath,
            idRender: properties.id,
            xpathContext: properties.xpathContext,
            idPageCache: properties.idPageCache,
            property: propertyName,
            extra: params,
            dataType: params ? params.dataType : null
        }).done(function (data) {
            if (data.type === "error") {
                // Workflow engine error, we gotta show it with error
                self.getFormContainer().addErrorMessage(data.message);
                defer.reject(data);
            } else {
                defer.resolve(data);
            }
        }).fail(function (jqXHR, textStatus, errorThrown) {
            self.getFormContainer().addErrorMessage(jqXHR.message);
            defer.reject(jqXHR, textStatus, errorThrown);
        });

        return defer.promise();
    },

    /**
     * Method to render non editable values
     */
    postRenderReadOnly: function () {
        this._super();
    },

    /**
     * Submits a link save request for the displayed form
     * @param data
     * @param defClose
     * @returns {*} Returns a deferred
     */
    submitSaveRequest: function (data, defClose) {
        var self = this;
        var properties = self.properties;

        // Don't send request when the link is not editable
        if (!properties.editable && !properties.value)
            return null;

        var xpath = self.getFormLinkXpath();
        var xpathContext = xpath;

        if (typeof (properties.xpathContext) !== "undefined" && properties.xpathContext !== "") {
            xpathContext = xpath ? properties.xpathContext + "." + xpath : properties.xpathContext;
        }

        return self.dataService.saveLinkForm({
            url: properties.saveUrl,
            idRender: properties.id,
            xpathContext: xpathContext,
            submitData: data
        });
    },

    getDisplayAsReadOnlyValue: function () {
        var self = this;
        return self.getFormContainer() && self.getFormContainer().properties.displayAsReadOnly;
    },

    closeExitRule: function () {
        var self = this;
        var properties = self.properties;
        var def = new $.Deferred();

        if (properties.hasexitrule && !self.getDisplayAsReadOnlyValue()) {
            $.when(self.openLinkRule("exitrule")).done(function (result) {
                def.resolve(result);
            }).fail(function () {
                def.reject();
            });
        } else {
            def.resolve();
        }

        return def.promise();
    },
    /**
     * Opens the link display form inside a dialog
     * @param elLink
     */
    openLink: function (elLink) {
        var self = this;
        var properties = self.properties;
        var form = self.getFormContainer();

        if (typeof (properties.canBeSaved) == "undefined") {
            properties.canBeSaved = true;
        }

        // When the link is not editable, then the form can't have save button
        if (properties.editable === false) {
            properties.canBeSaved = false;
        }

        if (form.properties.displayAsReadOnly) {
            properties.canBeSaved = false;
        }

        $.when(self.getFormContainer().properties.editable ? form.submitData() : undefined).done(function () {
            // Send edit request
            $.when(self.submitEditRequest()).done(function () {
                elLink.prop("disabled", false);

                // Show dialog with new form after that
                self.dialog = new bizagi.rendering.dialog.form(self.dataService, self.renderFactory, {
                    showSaveButton: properties.canBeSaved,
                    isPlanForm: properties.isPlanForm || false, //If this formLink is in a plan form
                    maximized: properties.maximized,
                    title: properties.displayName,
                    orientation: properties.orientation || "ltr",
                    onSave: function (data) {
                        var def = new $.Deferred();

                        var deferredsToWait = null;
                        if (data && data.deferreds) {
                            deferredsToWait = $.when.apply($, data.deferreds);
                            delete data.deferreds;
                        }

                        $.when(deferredsToWait).then(function () {

                            self.submitSaveRequest(data, def.promise()).done(function (result) {
                                var executeSubmitOnChange = true;
                                if (result && result.type === "validationMessages") {
                                    executeSubmitOnChange = false;
                                    def.resolve(result);
                                }
                                if (properties.hasexitrule) {
                                    self.closeExitRule().done(function () {
                                        if (executeSubmitOnChange) {
                                            $.when(self.submitCommitRequest(), self.submitDataRequest()).done(function () {
                                                var defer = null;
                                                if (properties.hasentryrule) {
                                                    defer = self.submitCommitRequest();
                                                }
                                                $.when(defer).done(function () {
                                                    form.refreshForm();
                                                });
                                            });
                                            def.resolve(result);
                                        }
                                    }).fail(function () {
                                        def.reject();
                                    });
                                } else if (executeSubmitOnChange) {
                                    if (executeSubmitOnChange) {
                                        def.resolve(result);
                                        self.submitCommitRequest().done(function(){
                                            self.submitDataRequest().done(function () {
                                                form.refreshForm();
                                            });
                                        });

                                        if (properties.hasentryrule)
                                            self.submitCommitRequest();
                                    }
                                }
                            });

                        });

                        return def.promise();
                    },
                    close: function () {
                    }
                });

                self.renderFormLinkDialog();
            });
        });
    },

    /**
     * Render Dialog
     */
    renderFormLinkDialog: function () {
        var self = this;
        var properties = self.properties;
        // DiegoP: Commented the following lines because it was generating a side effect bug, and it was only intended for plans
        // The idea is that when we use plans, it opens a form link (of the parent activity), and the whole page must be optional (without required fields)
        // but now  when the there is a formlink inside the parent form; it's opening the target form  but the required fields are still required
        // var required = bizagi.util.parseBoolean(properties.required) !== null ? bizagi.util.parseBoolean(properties.required) : true;

        self.dialog.render({
            idRender: properties.id,
            xpathContext: self.getFormLinkXpathContext(),
            idPageCache: properties.idPageCache,
            recordXPath: self.getFormLinkXpath(),
            requestedForm: "linkform",
            editable: properties.editable,
            //required: required,
            url: properties.editPage,
            mainForm: self.getFormContainer ? self.getFormContainer() : null
        }).fail(function (data) {
            // Sends a rollback request to delete checkpoints
            $.when(self.submitRollbackRequest(data)).done(function () {
                //Sends additional rollback request to delete checkpoint from entry rule
                if (properties.hasentryrule){
                    $.when(self.submitRollbackRequest(data)).done(function () {
                        self.submitInvalidatePageCacheRequest(data);
                    });
                }
                else{
                    self.submitInvalidatePageCacheRequest(data);
                }
            });
        });
    },

    getFormLinkXpathContext: function () {
        var self = this;
        var xpathContext = self.properties.xpathContext;
        var parentXpathContext = self.getXpathContext();

        if (parentXpathContext && xpathContext) {
            if (xpathContext.indexOf(parentXpathContext) === 0) {
                return xpathContext;
            } else {
                return parentXpathContext + "." + xpathContext;
            }
        } else {
            return xpathContext;
        }
    },

    /**
     * Configure event handlers
     */
    attachEvents: function () {
        var self = this;
        var control = self.getControl();
        var link = $(".ui-bizagi-render-link", control);
        var properties = self.properties;

        // Define dialog
        self.dialog = null;

        // Bind click event
        link.click(function () {
            // Open the link inside a dialog
            var elLink = $(this);
            if (properties.hasentryrule && !self.getDisplayAsReadOnlyValue()) {
                var form = self.getFormContainer();
                elLink.prop("disabled", true);

                $.when(form.submitData()).done(function () {
                    self.submitCheckpointRequest();
                    self.openLinkRule("entryrule", elLink);
                });
            } else {
                elLink.prop("disabled", true);
                self.openLink(elLink);
            }
        });
    },

    /**
     * Customizes render color
     * @param color
     */
    changeColor: function (color) {
        var self = this;
        var control = self.getControl();
        var label = self.getLabel();

        if (control && label) {
            if (color !== "none") {
                self.getControl().find("a").css("color", color, "!important");

                if (typeof (self.getControl().find("a").style) == "function") {
                    self.getControl().find("a").style("color", color, "!important");
                }
            } else {
                self.getControl().find("a").css("color", "");

                if (typeof (self.getControl().find("a").style) == "function") {
                    self.getControl().find("a").style("color", "");
                }
            }
        }
        self._super(color);
    },
    
    changeFontBold: function (){
        var self = this;
        var control = self.getControl().children();
        var textFormat = self.properties.textFormat;
        if(typeof textFormat.bold !== "undefined"){
            if (typeof control == "object" && control.length > 0) {
                self.getElementsToStylize(control).css("font-weight", "bold", "!important");
            }    
        }
    }
});
