/**
 *   Name: Bizagi User Field render Desktop Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define the basic behaviour for userfields
 */

bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.userfieldWrapper", bizagi.rendering.userfieldWrapper.prototype);
bizagi.rendering.desktop.userfieldWrapper.extend("bizagi.rendering.desktop.userfieldWrapper", {}, {

    /**
     *  Load userfield
     */
    getUserfieldControl: function () {
        var self = this;
        var sControl = self.userfieldName;
        var mode = self.getMode();
        var exists = (mode !== "execution") || eval("bizagi.rendering.desktop." + sControl + " !== undefined");
        var def = new $.Deferred();

        if (exists) {
            // Creates the userfield instance
            // Set true argument for tabItem
            $.when(self.parent.ready()).done(function () {
                def.resolve(self.createUserfieldInstance(sControl));
            });

        } else {
            // Call ajax service in order to retrieve user field definitions
            $.when(self.dataService.getUserfieldDefinition({
                userfield: self.userfieldGuid,
                device: "desktop"
            })).pipe(function (data) {
                // Load userfield in memory
                self.loadUserfield(data);

                // Creates the userfield instance
                def.resolve(self.createUserfieldInstance(sControl));
            });
        }

        return def.promise();
    },

    /**
     * Creates the userfield instance when the userfield prototype is loaded in memory
     */
    createUserfieldInstance: function (sControl) {
        var self = this;
        var control;
        try {
            // Create a dynamic function to avoid problem with eval calls when minifying the code
            var dynamicFunction = "var baDynamicFn = function(renderParams){ \r\n";
            dynamicFunction += "return new bizagi.rendering.desktop." + sControl + "(renderParams);\r\n";
            dynamicFunction += "}\r\n";
            dynamicFunction += "baDynamicFn";
            dynamicFunction = eval(dynamicFunction);

            // Call the dynamic function
            control = dynamicFunction(self.initialParams);
        } catch (e) {
            // If the call fails, return the default user field
            control = self.getUserFieldInstance(self.initialParams);
        }

        control.properties.isUserField = true;

        // Save userfield instance
        self.userfieldInstance = control;

        // Create proxy methods from the wrapper to the userfield instance
        self.replicateUserfieldMethods(control);

        // Replicate renderchange event from wrapped element to grid if applies
        self.userfieldInstance.bind("renderchange", function () {
            self.triggerHandler("renderchange", arguments);
        });

        return control;
    },

    /**
     * Load userfield into memory
     */
    loadUserfield: function (data) {
        var self = this;
        var styleContent = "";

        self.properties.styleGuid = self.properties.styleGuid || Math.guid();

        try {
            // Parse data
            try {
                data = eval(data);
            } catch (e) {
                bizagi.log("Could not parse result userfield " + self.userfieldName, e.message);
            }

            // Iterate elements
            $.each(data, function (index, val) {
                if (val.type === "js") {
                    try {
                        function replaceWithDevice(str) {
                            return str.replace(/bizagi.rendering/gi, "bizagi.rendering.desktop");
                        }

                        val.content = val.content.replace(/bizagi\.rendering\.[^\s]+\.extend(\s|\n|\s)*\((\s|\n|\s)*\"bizagi\.rendering\.[^\s]+\"/gmi, replaceWithDevice);
                        window.eval(val.content);
                    } catch (ex) {
                        bizagi.log("Could not parse result userfield3 " + self.userfieldName, ex.message);
                    }
                }
                if (val.type === "css") {
                    styleContent += val.content;
                }
            });

            if ($.trim(styleContent) !== "") {
                bizagi.util.loadStyle(styleContent, self.properties.styleGuid);
            }

            // Replicate userfield methods to the wrapper
            var control = eval("bizagi.rendering." + self.userfieldName);

        } catch (e) {
            bizagi.log("Could not parse result userfield " + self.userfieldName, e.message);
        }
    },

    getUserFieldInstance: function (params) {
        var mode = this.getMode();

        // If the call fails, return the default user field
        return (mode === "execution") ? new bizagi.rendering.desktop.defaultUserField(params) : new bizagi.rendering.desktop.designUserField(params);
    }
});

// Auto-extension
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.basicUserField", bizagi.rendering.userfieldDefinition.prototype);
bizagi.rendering.desktop.basicUserField.extend("bizagi.rendering.desktop.basicUserField", bizagi.rendering.basicUserField.prototype);
bizagi.rendering.desktop.basicUserField.extend("bizagi.rendering.desktop.basicUserField", {}, {
    /*
    * Shows a jquery dialog
    */
    showDialog: function (url, params) {
        var self = this;
        var formDialog = $("<div class='iframeContent'><iframe scrolling='yes' id='innerFrameContent' src='" + url + "' style='width:100%;height:100%;border:0'  /></div>");
        var iframe = $("#innerFrameContent", formDialog);

        // The following procedure adds the "closeWindow" function, this will serve as callback from the
        // window, also it's going to serve as brigde to update data back into bizagi fields.
        iframe.load(function () {
            iframe.callInside(function (params) {
                // This script will execute inside the iframe context
                this.BACloseWindow = function (controls, submitForm) {
                    // close dialog
                    params.formDialog.dialog("close");

                    // Executes the callback to assign values and submit form (if requested)
                    params.formDialog.callback(controls, submitForm);
                };

            }, {
                formDialog: formDialog,
                container: self
            });

            // Callback function which will update the values in bizagi
            formDialog.callback = function (controls, submitForm) {
                $.each(controls, function (index, item) {
                    if (!item.xpath) {
                        bizagi.showMessageBox('Error calling closeWindow, the xpath was not defined.', 'Userfield error');
                    }
                    if (!item.value) {
                        bizagi.showMessageBox('Error calling closeWindow, the value was not defined.', 'Userfield error');
                    }

                    self.getFormContainer().getRender(item.xpath).setDisplayValue(item.value);
                });

                // If the closeWindow was call with "submitForm" then execute the submit on change
                if (submitForm) {
                    self.submitOnChange();
                }
            };
        });

        formDialog.dialog({height: params.height, width: params.width, title: params.title});
    },

    /*
    *   Shows a browser popup
    */
    showPopup: function (url, params) {
        var self = this;

        // Callback function which will update the values in bizagi
        var callbackFn = function (controls, submitForm) {
            $.each(controls, function (index, item) {
                if (!item.xpath) {
                    bizagi.showMessageBox('Error calling closeWindow, the xpath was not defined.', 'Userfield error');
                }

                if (!item.value) {
                    bizagi.showMessageBox('Error calling closeWindow, the value was not defined.', 'Userfield error');
                }

                self.getFormContainer().getRender(item.xpath).setDisplayValue(item.value);
            });

            // If the closeWindow was call with "submitForm" then execute the submit on change
            if (submitForm) {
                self.submitOnChange();
            }
        };

        bizagi.showPopup(url, params, callbackFn);
    }
});

/*
*   Default implementation for user fields
*/
bizagi.rendering.desktop.basicUserField.extend("bizagi.rendering.desktop.defaultUserField", {}, {

    /*
    *   Template method to implement in each device to customize each render after processed
    */
    getEditableControl: function () {
        var self = this;
        var control = self.getControl();
        var properties = self.properties;
        var controlName = properties.control;

        self.input = $("<label>Userfield: " + controlName + " not found</label>").appendTo(control);
    },

    /*
	*  Dont send the data to the server
	*/
    collectData: function (renderValues) {
    },

    getDisplayValue: function () {
        var self = this;
        var properties = self.properties;
        var controlName = properties.control;

        return "Userfield: " + controlName + " not found";
    },

    getReadonlyControl: function () {
        var self = this;
        var control = self.getControl();
        var properties = self.properties;
        var controlName = properties.control;
        var mycontrol = $("<label>Userfield: " + controlName + " not found</label>").appendTo(control);

        return mycontrol;
    }
});